<template>
  <div>
    <el-dialog :title="update==true?'菜单添加':'菜单修改'" :visible.sync="dialogFormVisible">
      
      <el-form :model="form">
        <el-form-item label="所属角色" :label-width="formLabelWidth">
          <el-select v-model="form.roleid">
            <el-option :label="item.rolename" :value="item.id"
            v-for="(item,index) in roleList" :key="index"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item
          label="用户名"
          :label-width="formLabelWidth"
        >
          <el-input v-model="form.username" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item
          label="密码"
          :label-width="formLabelWidth"
        >
          <el-input v-model="form.password" autocomplete="off"></el-input>
        </el-form-item>

        

        <el-form-item label="状态">
            
          <el-switch
            v-model="form.status"
            :active-value="1"
            :inactive-value="2"
            active-color="#13ce66"
            inactive-color="#ff4949">
          </el-switch>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancle">取 消</el-button>
        <el-button type="primary" @click="adduserList" v-if="update">添加</el-button>
        <el-button type="primary" @click="updateuser" v-else>修改</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import { mapGetters, mapActions } from "vuex";
import {alertSuccess,alertWarn} from '../../../utils/alert'
import { listuserAdd ,updateuserAct,userinfo} from "../../../utils/request";
export default {
  props: ['update'],
  components: {},
  data() {
    return {
      dialogFormVisible: false,
      form: {
        roleid:6,
        username: "",
        password: "",
        status: 0,
      },
      formLabelWidth: "120px",
    };
  },
  computed: {
    ...mapGetters({
      roleList:"roleList"
    }),
  },
  methods: {
    ...mapActions({
        getRoleList:"getRoleList",
        getuserList:"getuserList",
        reqTotal1:"reqTotal1"
    }),
    cancle(){
        this.dialogFormVisible=false;
        this.clearForm()
    },
    clearForm(){
      this.form={
        roleid: 6,
        username: "",
        password: "",
        status: 0,
      }
    },
    adduserList() {
      this.dialogFormVisible = false;
      listuserAdd(this.form).then(res=>{
        this.getuserList()
        this.clearForm()
        this.reqTotal1()
      })
    },
    getuserDetail(id){
      userinfo(id).then(res=>{
        this.form=res.data.list
        this.form.uid=id
      })
    },
    updateuser(){
      updateuserAct(this.form).then(res=>{
        if(res.data.code==200){
                alertSuccess(res.data.msg)
                this.dialogFormVisible = false;
                this.clearForm()
                this.getuserList()
              }else{
                alertWarn(res.data.msg)
              }
      })
    },
    
  },
  mounted(id) {
      if(this.roleList.length==0){
        this.getRoleList()
      }
  },
};
</script>
<style scoped>
</style>


