<template>
  <div>
    <el-table
      :data="userList"
      style="width: 100%; margin-bottom: 20px"
      row-key="id"
      border
      :tree-props="{ children: 'children', hasChildren: 'hasChildren' }"
    >
      <el-table-column prop="id" label="用户编号" sortable width="180">
      </el-table-column>
      <el-table-column prop="username" label="用户名" sortable width="180">
      </el-table-column>
      <el-table-column prop="rolename" label="所属角色"> </el-table-column>
      <el-table-column prop="status" label="状态">
        <template slot-scope="scope">
          <el-button type="primary" v-if="scope.row.status == 1"
            >启用</el-button
          >
          <el-button type="primary" v-if="scope.row.status == 2"
            >禁用</el-button
          >
        </template>
      </el-table-column>
      <el-table-column prop="icon" label="操作">
        <template slot-scope="scope">
          <el-button type="primary" @click="edit(scope.row.uid)"
            >修改</el-button
          >
          <el-button type="danger" @click="del(scope.row.uid)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination background layout="prev, pager, next" :total="total" :page-size="size"
    @current-change="CurrentChange"></el-pagination>
    
  </div>
</template>
<script>
import { mapGetters, mapActions } from "vuex";
import { userDel } from "../../../utils/request";
import { alertWarn, alertSuccess } from "../../../utils/alert";
export default {
  props: [],
  components: {},
  data() {
    return {};
  },
  computed: {
    ...mapGetters({
      tableData: "menuList",
      userList: "userList",
      total:"total",
      size:"size",
    }),
  },
  methods: {
    ...mapActions({
      getMenuList: "getMenuList",
      getuserList: "getuserList",
      reqTotal1:"reqTotal1",
      reqchangepage:"reqchangepage"
    }),
    edit(id) {
      this.$emit("update", id);
    },
    del(id) {
      this.$confirm("是否确认删除?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          userDel(id).then((res) => {
            if (res.data.code == 200) {
              alertSuccess(res.data.msg);
              this.getuserList();
              this.reqTotal1()
            } else {
              alertWarn(res.data.msg);
            }
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },
    CurrentChange(e){
      this.reqchangepage(e)
    }
  },
  mounted() {
    this.getMenuList();
    if (this.userList.length == 0) {
      this.getuserList();
    }
    this.reqTotal1()
  },
};
</script>
<style scoped>
</style>


